import React from 'react'
import {Link, useLocation} from "react-router-dom";


const UserItem = ({user}) => {
    return (
        <tr>
            <td>
                {user.first_name}
            </td>
            <td>
                {user.last_name}
            </td>
            <td>
                {user.user_name}
            </td>
            <td>
                {user.email}
            </td>
            <td>
                {user.user_category}
            </td>
            <td>
                <Link to={`${user.uid}`}> </Link>
            </td>
        </tr>
    )
}

const UserList = ({users}) => {
    return (
        <table>
            <caption></caption>
            <thead>
                <tr>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                </tr>
            </thead>
            <tbody>
                {users.map((user) => <UserItem user={user}  key={user.uid}/>)}
            </tbody>
        </table>
    )
}
export default UserList